	module.exports = {"pathContext":{"group":[{"node":{"fields":{"title":"Ongoing Concern","date":"02 Jun 2004","slug":"/2004-06-02-ongoing-concern/","tags":"Y2004"},"html":"<p>Despite missing most of last week at work, I apparently didn't miss much. There is still no official word on whether we have avoided insolvency or not. There are all sorts of rumors, ranging from the company will declare insolvency but continue in its present form to  we will be bought out and absorbed into an existing firm. A persistent rumor is that systems administration will be completely out-sourced. </p>\n<p>What we do know for sure is that 1) the May salaries are late, and 2) short-time <em>(Kurzarbeit)</em> has been extended another 6 months until November. So someone is planning at least for the short-term future. And the stock price been going up (to 70 cents from 30 cents last week), so somebody might know something.</p>\n<p>(Oh, and my knee is doing better. It still hurts, but it's now a tired kind of hurt, and I've put the crutches away. After returning to work yesterday, I now realize just how many stairs there are in the Hamburg U- and S-Bahn system. I feel each and every one of them.)</p>"},"next":{"fields":{"title":"News Flash","date":"02 Jun 2004","slug":"/2004-06-02-news-flash/"}},"previous":{"fields":{"title":"Very Little Code","date":"02 Jun 2004","slug":"/2004-06-02-very-little-code/"}}},{"node":{"fields":{"title":"News Flash","date":"02 Jun 2004","slug":"/2004-06-02-news-flash/","tags":"Y2004"},"html":"<p>It was announed this evening that an investor has bought 1/4 of my employer's stock and is injecting 400k cash. It looks like my job is saved... for now.</p>"},"next":{"fields":{"title":"Hamburger BlogGrill","date":"01 Jun 2004","slug":"/2004-06-01-hamburger-bloggrill/"}},"previous":{"fields":{"title":"Ongoing Concern","date":"02 Jun 2004","slug":"/2004-06-02-ongoing-concern/"}}},{"node":{"fields":{"title":"Hamburger BlogGrill","date":"01 Jun 2004","slug":"/2004-06-01-hamburger-bloggrill/","tags":"Y2004"},"html":"<p>Hamburger <a title=\"Why Walk or Talk when you can Grill?\" href=\"http://wiki.blogg.de/wakka.php?wakka=BlogGrill\">BlogGrill</a>: 10 June, 20:00, Hamburg. See <a title=\"Why Walk or Talk when you can Grill?\" href=\"http://wiki.blogg.de/wakka.php?wakka=BlogGrill\">Wiki</a> for address and sign up.</p>"},"next":{"fields":{"title":"German Top 50 Blogparade","date":"01 Jun 2004","slug":"/2004-06-01-german-top-50-blogparade/"}},"previous":{"fields":{"title":"News Flash","date":"02 Jun 2004","slug":"/2004-06-02-news-flash/"}}}],"pathPrefix":"","first":false,"last":false,"index":475,"pageCount":1101}}

